<template lang="pug">
    .tests__content
        .tests__header
            h4.tests__title Список составленных тестов
            ul.tests__filter
                li.tests__filter-item
                    label.label__filter-tests Выберите предмет:
                    select(v-model="selectedSubject").tests__filter-select
                        option(value=0) Все
                        option(:value='item.id' v-for='item in subjects') {{item.name}}
                li.tests__filter-item
                    label.label__filter-tests(for='classes') Выберите класс:
                    select(v-model="selectedClass").tests__filter-select
                        option(:value='0') Все
                        option(:value='item' v-for='item in 12') {{item}}
                li.tests__filter-item
                    label.label__filter-tests Выберите язык:
                    select(v-model="selectedLanguage").tests__filter-select
                        option(value='') Все
                        option(value='Казахский') Казахский
                        option(value='Русский') Русский
        ul.tests__list
            .tests__item.names
                .test__item-row
                    .test__item-row.names
                    .tests__number #
                    .tests__code Код теста
                    .tests__subject Предмет
                    .tests__class Класс
                    .tests__class Время (мин)
                    .tests__pass Выбрать
            li.tests__item(v-for="test,idx in filteredTests" :key="test.id")
                available-test-item(
                    :idx="idx"
                    :test="test"
                    )
        button(@click.prevent='addTestForm' id='add__new-test').add__test-button Добавить тест
        new-test(
            :user='user'
            v-if='newTestVisible'
            v-on:eventNewTest='eventNewTest'
            )

</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        availableTestItem: () => import("../../teacher/tests/available-test-item"),
        newTest: () => import("../../teacher/tests/new-test")
    },
    created() {
        this.fetchTests(this.user.id),
        this.fetchSubjects()
    },
    computed: {
        ...mapState ("tests", {
            tests: state => state.tests}),
        ...mapState ("user", {
            user: state => state.user}),
        ...mapState ("subjects", {
            subjects: state => state.subjects}),
        filteredTests: function() {
            return this.tests
            //filter by subject
            .filter(test => {
                return this.selectedSubject == 0 || test.group.subject.id== this.selectedSubject;
            })
            //filter by class
            .filter(test => {
                return this.selectedClass == 0 || test.group.groupClass== this.selectedClass;
            })
            //filter by language
            .filter(test => {
                return this.selectedLanguage == '' || test.group.language== this.selectedLanguage;
            })
        }
    }, 
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdn.mathjax.org/mathjax/latest/MathJax.js?config=TeX-MML-AM_CHTML')
        document.head.appendChild(recaptchaScript)

    },
    methods: {
        ...mapActions("tests", ["fetchTests"]),
        ...mapActions("subjects", ["fetchSubjects"]),
        eventNewTest() {
            this.newTestVisible=!this.newTestVisible
            document.getElementById('add__new-test').removeAttribute('disabled')
            this.fetchTests(this.user.id)
        },
        addTestForm() {
            if(this.newTestVisible)
                document.getElementById('add__new-test').setAttribute('disabled', true)
            else{
                this.newTestVisible=!this.newTestVisible
            }
        }
    },
    data () {
        return {
            newTestVisible: false,
            selectedSubject: 0,
            selectedClass: 0,
            selectedLanguage: ''
        }
    }
}
</script>
